@import "variables";

@page {
    background: #fff;
    margin: 0;
    background: $color-purple;
}
* {
    box-sizing: border-box;
}
body {
    background: $color-purple;
    background: $color-gray-95;
}
#header {
    text-align: center;
    color: #fff;
    background: $color-purple;
    padding: 20pt 10.64pt;

    .title {
        font-size: 32px;
        margin-bottom: 2px;
        font-weight: 900;
    }
}
#main {
    padding: 12pt 10.64pt;
}
.subtitle {
    font-size: 16px;  
}
.date {
    font-size: 13px;  
    text-transform: uppercase;
    letter-spacing: 0.05em;
    font-weight: 900;
}
.quick-stats:after {
    content: "";
    clear: both;
    display: table;
}
.stat {
    position: relative;
    width: 212px;
    margin-right: 8px;
    padding: 18px;
    background-color: #fff;
    border: solid 1px $color-gray-90;
    border-radius: 6px;
    float: left;

    &:last-child {
        margin-right: 0;
    }
    &:after {
        content: '';
        display: inline-block;
        position: absolute;
        top: 18px;
        right: 18px;
        width: 10pt;
        height: 10pt;
        border-radius: 10pt;
        background: $color-purple;
    }
    &.visitors:after {
        background: $color-orange;
    }
    &.sessions:after {
        background: $color-red;
    }
}
.values {
    font-weight: 700;
    margin: 8pt 0 0;
}
.count,
.growth,
.percentage {
    display: inline-block;
    vertical-align: middle;
}
.count {
    font-size: 28px;
    margin-right: 2pt;
}
.growth {
    font-size: 14px;
    color: $color-green;
}
.percentage {
    padding: 4px 10px 4px 12px;
    border-radius: 24px;

    &.up {
        background-color: $color-green-light;
        color: $color-green;
    }
    &.down {
        background-color: $color-red-light;
        color: $color-red;
    }
}
.period-label {
    font-size: 16px;
    color: $color-gray-65;
}
#chart {
    padding: 18px 24px 36px 48px;
    margin: 12pt 0;
    background: #fff;
    border: solid 1px $color-gray-90;
    border-radius: 6px;
}
.labels {
    font-size: 10pt;
    margin: 0 0 18pt -18pt;

    span {
        margin-right: 6pt;
    }
}
.label-date {
    font-weight: 900;
}
.label-views:before,
.label-visitors:before,
.label-sessions:before {
    content: '';
    display: inline-block;
    width: 8pt;
    height: 8pt;
    border-radius: 8pt;
    background: $color-purple;
    margin-right: 4pt;
}
.label-visitors:before {
    background: $color-orange;
}
.label-sessions:before {
    background: $color-red;
}
#top-results:after,
.top-ten:after {
    content: "";
    clear: both;
    display: table;
}
.top-ten {
    display: inline-block;
    vertical-align: top;
    width: 161pt;
    height: 220pt;
    background: #fff;
    border-radius: 6px;
    border: solid 1px $color-gray-90;
    padding: 12pt;
    margin-left: 7pt;

    &:first-child {
        margin-left: 0;
    }
    .heading {
        font-size: 9pt;

        &:after {
            content: "";
            clear: both;
            display: table;
        }
    }
    .title-med {
        float: left;
        font-weight: 900;
    }
    .views-heading {
        float: right;
    }
    ol {
        font-size: 9pt;
        clear: both;
        margin: 12pt 0 0 0;
        padding: 0 0 0 12pt;
    }
    li {
        margin-bottom: 4pt;
        padding-bottom: 4pt;
        border-bottom: dotted 1px $color-gray-90;

        .views {
            float: right;
        }
    }
}
#footer {
    margin-top: 12pt;
    padding: 12pt 10.64pt 0;
    text-align: center;

    p {
        font-size: 10pt;
        margin: 0;
    }
     a {
        color: $color-purple;
    }
}